*{font-family:Helvetica, Arial, sans-serif;padding:0;margin:0}html{font-size:62.75%}body{background-color:#333745}button{cursor:pointer}button:disabled{cursor:default}.add-option-error{color:#a5afd7;font-style:italic;font-size:1.6rem;padding:0 1.6rem 1.2rem 1.6rem;text-align:center;margin-top:1.6rem}.add-option{display:flex;flex-direction:column;padding:1.2rem;justify-content:space-between}.add-option__input{background:#333745;border:none;border-bottom:0.3rem solid #1d2028;border-radius:.75rem;margin-bottom:1.2rem;padding:1.2rem;color:#a5afd7;font-size:1.6rem}@media screen and (min-width: 415px){.add-option{flex-direction:row}.add-option__input{flex-grow:.95}}.big-button{background-color:#8357c5;border:none;border-bottom:0.6rem solid #693cad;color:white;font-weight:bold;font-size:1.6rem;width:100%;border-radius:1.5rem;padding:2.4rem;margin-bottom:1.6rem}.big-button:disabled{opacity:.5}@media screen and (min-width: 415px){.big-button{font-size:3.2rem;margin-bottom:4.8rem}}.button{background-color:#8357c5;border:none;border-radius:.75rem;border-bottom:0.3rem solid #693cad;color:white;font-size:1.6rem;font-weight:500;padding:1.2rem}.button--link{background:none;border:none;color:#a5afd7;padding:0}.container{max-width:60rem;margin:0 auto;padding:0 1.6rem}.header{background:#20222b;margin-bottom:1.6rem;padding:3.2rem 0;color:white}.header__title{font-size:3.2rem;margin:0}.header__subtitle{color:#a5afd7;font-size:1.6rem;font-weigth:500}@media screen and (min-width: 415px){.header{margin-bottom:4.8rem}}.ReactModalPortal>div{opacity:0}.ReactModalPortal .ReactModal__Overlay{display:flex;justify-content:center;align-items:center;transition:opacity 250ms ease-in-out}.ReactModalPortal .ReactModal__Overlay--after-open{opacity:1}.ReactModalPortal .ReactModal__Overlay--before-close{opacity:0}.modal{background:#464b5e;padding:3.2rem;color:white;max-width:30rem;outline:none;text-align:center}.modal__title{font-size:2rem;padding-top:1.2rem}.modal__body{font-size:1.6rem;font-weight:300;padding:1.6rem;word-break:break-all}.option{border-bottom:1px solid #5c627b;display:flex;justify-content:space-between;padding:3.2rem 1.6rem}.option__text{font-size:1.6rem;color:white;font-weight:500;word-break:break-all}.widget{background:#464b5e;margin-bottom:4.8rem;border-radius:.75rem}.widget--message{color:#a5afd7;padding:3.2rem;text-align:center;border-bottom:1px solid #5c627b;font-size:1.6rem}.widget-header{background-color:#3c4251;color:#a5afd7;padding:1.6rem;font-size:1.6rem;display:flex;justify-content:space-between;border-top-left-radius:.75rem;border-top-right-radius:.75rem}
